import React, { Component } from 'react';

const CommentItem = (props) => {
	return (
		<div>
			<i style={{fontStyle: 'italic', color:"#a0a0a0"}}>"Sorry Comments Cannot Be Viewed At This Time"</i>
		</div>
	);
}

export default CommentItem;



